.about__container {
	display: grid;
	grid-template-columns: 35% 50%;
	gap: 15%;
}

.about__me {
	aspect-ratio: 1/1;
	width: 100%;
	border-radius: 2rem;
	background: linear-gradient(45deg, transparent, var(--color-alternative), transparent);
}

.about__me-image {
	border-radius: 2rem;
	overflow: hidden;
	transform: rotate(10deg);
	transition: var(--transition);
}

.about__me-image img {
	aspect-ratio: 1/1;
}

.about__me-image:hover {
	transform: rotate(0);
}

.about__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 1.5rem;
}

.about__card {
	background: var(--color-alternative);
	color: var(--color-bg);
	border: 1px solid transparent;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
	transition: var(--transition);
}

.about__card:hover {
	background: transparent;
	border-color: var(--color-alternative);
	color: var(--color-alternative);
	cursor: default;
}

.about__icon {
	color: var(--color-alternative);
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.about__card h5 {
	font-size: 0.95rem;
}

.about__card small {
	font-size: 0.7rem;
}

.about__content p {
	margin: 2rem 0 2.6rem;
	color: var(--color-alternative);
}

/* ===== Медиа-запросы (для < 1200px) */

@media screen and (max-width: 1200px) {
	.about__container {
		grid-template-columns: 1fr;
		gap: 0;
	}

	.about__me {
		width: 50%;
		margin: 2rem auto 4rem;
	}

	.about__content p {
		margin: 1rem 0 1.5rem;
	}
}

/* ===== Медиа-запросы (для < 600px) */

@media screen and (max-width: 600px) {
	.about__me {
		width: 65%;
		margin: 0 auto 3rem;
	}

	.about__content {
		text-align: center;
	}

	.about__content p {
		margin: 1.5rem 0;
	}

	.about__cards {
		grid-template-columns: 1fr;
	}
}
